
.dlg-floow-twitter-quest {
    /* POPUP */

    /* Auto layout */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    width: 600px;
    height: 406px;

    background: rgba(38, 34, 51, 0.95);
    border-radius: 12px;
}

.tnq-header {
    display: flex;
    padding: 16px;
}

.tnq-title {
    /* Aria Mission */

    margin: 0 auto;

    height: 28px;

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    /* identical to box height */

    color: #F1F1F1;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    text-align: left;

    margin-top: 35px;

}

.tnq-content {
    /* Frame 8985 */
    margin-top: 15px;
    box-sizing: border-box;

    width: 510px;
    height: 109px;

    background: #3D3949;
    border: 1px solid rgba(224, 224, 224, 0.7);
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 18px;
}

.tnq-content-logo {
    /* Frame 37904 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    flex: 1;

    /* width: 105px; */
    height: 46px;

}

.tnq-content-logo>div:nth-child(1) {

    /* Frame 37902 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 8px;
    gap: 10px;

    width: 54px;
    height: 54px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    box-sizing: border-box;
}

.tnq-content-logo>div:nth-child(1)>img {
    /* image 114 */

    width: 38px;
    height: 38px;

    background: url('../../assets/images/x-logo1.png');
    border-radius: 20px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

}


.tnq-content-logo .name {

    /* Frame 37903 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 8px 8px 8px 0px;
    gap: 10px;

    /* width: 51px; */

    /* Inside auto layout */
    flex: none;
    order: 1;
    flex-grow: 0;

    white-space: nowrap;
    /* Aria */

    /* width: 43px; */
    /* height: 31px; */

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 31px;
    /* identical to box height */

    color: #F1F1F1;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

}

.tnq-content-username {
    /* Frame 8984 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px 0px 0px 5px;
    gap: 32px;


    width: 197px;
    height: 24px;


    /* @PlayAriaGame · Follow */

    width: 192px;
    height: 24px;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height */

    color: #C5C5C5;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

    /* Vector

    position: absolute;
    left: 2.5%;
    right: -2.5%;
    top: 5.69%;
    bottom: 5.74%;

    background: #FFFFFF; */
}

.tnq-content .xlogo>img {
    /* SVG */
    width: 38px;
    height: 46px;
    margin-right: 41px;
}

.tnq-follow-link {
    text-decoration: none;
    color: #35f0d0;
}

.tnq-footer {}

.tnq-validate-btn {
    /* Button */
    box-sizing: border-box;

    /* Auto layout */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 20px 92px;
    gap: 10px;


    width: 410px;
    height: 60px;


    background: #FFE4E4;
    border: 1px solid rgba(224, 224, 224, 0.7);
    border-radius: 10px;
    cursor: pointer;
    margin-top: 45px;

}