.home {
  /* width: 1920px; */
  height: 100vh;
  aspect-ratio: 1920/1040;
}
.home-video {
  width: 100%; /* 宽度为100% */
  height: 100%; /* 高度为100% */
  position: fixed;
  top: 0;
  left: 0;
  object-fit: cover;
  z-index: 0;
}
.home-mask > div {
  border-radius: 12px;
  border: 1px solid #ffffff;
  width: 100%;
  height: calc(100% - 20px);
  margin: 10px;
}
.home-mask-1 {
  width: 100%;
  height: 100%;
  z-index: 0;
  top: 0;
  left: 0;
  background: radial-gradient(
    41.43% 41.43% at 50% 50%,
    rgba(47, 0, 103, 0.5184) 0%,
    rgba(18, 18, 18, 0.3888) 100%
  );
}
.home-mask-2 {
  width: 100%;
  height: 100%;
  z-index: 0;
  top: 0;
  left: 0;
  background: linear-gradient(
    180deg,
    rgba(13, 3, 29, 0.6) 0%,
    rgba(0, 0, 0, 0) 50%,
    rgba(13, 3, 29, 0.6) 100%
  );
}
.home-mask-3 {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  background: linear-gradient(
    90deg,
    rgba(13, 3, 29, 0.34) 0%,
    rgba(0, 0, 0, 0) 50%,
    rgba(13, 3, 29, 0.34) 100%
  );
}
.home-poster-0 {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url(../../assets/images/home-video-poster.png);
  background-repeat: no-repeat;
  object-fit: cover;
  z-index: -1;
}
.home-poster-1 {
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
  background: url(../../assets/images/home-video-poster.jpg);
  overflow: hidden;
}
.home-poster-1 > video {
  width: 100%;
  height: 150%;
  object-fit: cover;
  transform: translate(-0%, -18%);
  overflow: hidden;
}
.home-logo {
  width: 400px;
  height: 400px;
  margin-top: 86px;
}
.home-logo > img {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.home-text {
  font-family: Bakbak One;
  font-size: 47px;
  font-weight: 400;
  line-height: 80px;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
  margin-top: -30px;
  text-transform: uppercase;
}
.home-sub {
  //styleName: Body/01;
  font-family: Poppins;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: center;
  color: #ffffff;
  width: 630px;
}
.home-connect {
  width: 133px;
  height: 48px;
  border-radius: 10px;
  border: 1px solid #ffffff;
  font-family: Bakbak One;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
  line-height: 48px;
  color: #ffffff;
  margin-top: 28px;
  cursor: pointer;
}
.home-radio {
  width: 56px;
  height: 56px;
  border-radius: 38px;
  margin-top: 113px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-radio > img {
  width: 32px;
  height: 32px;
  border-radius: 16px;
}
.layout-ml14 {
  margin-left: 14px;
}
.layout-mr14 {
  margin-right: 14px;
}
.home-player-groups {
  text-decoration: none;
  position: absolute;
  bottom: 0;
  left: 0;
}
.home-player-groups a {
  text-decoration: none;
}
.play-game {
  width: 420px;
  height: 96px;
  border-radius: 12px;
  border: 1px solid #ececec;
  background: rgba(255, 255, 255, 0.8);
  position: relative;
  margin-bottom: 14px;
  display: none;
}
.play-game > img {
  position: absolute;
  width: 234px;
  height: 161px;
  top: -66px;
  left: 0;
}
.play-game > div > span {
  font-family: Bakbak One;
  font-size: 22px;
  font-weight: 500;
  letter-spacing: 0em;
  text-align: left;
  color: rgba(0, 0, 0, 1);
  margin-right: 8px;
}
.play-game > div > img {
  width: 36px;
  height: 36px;
  display: inline-block;
  margin-right: 22px;
}
.home-footer {
  height: 529px;
  position: relative;
  /*background: linear-gradient(93.51deg, rgba(155, 81, 224, 0.54) 2.84%, rgba(48, 129, 237, 0.54) 99.18%);*/
}
.home-footer > img {
  width: 100%;
  height: 100%;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
}
.home-footer-album {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 333;
}
.home-footer-album > div {
  width: calc(100% - 400px);
  margin-left: 200px;
  margin-right: 200px;
  margin-top: 25px;
  flex-wrap: wrap;
}
.home-footer-album > div > img {
  width: calc(calc(100% - 400px - 16px) / 4) px;
  margin-right: 4px;
  height: 180px;
  display: inline-block;
  object-fit: cover;
  margin-top: 4px;
}

