.team-container {
    background-image: url('../../assets/images/team/team-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    /* min-height: 1080px; */
    /* width: 1920px; */
}

.team-container-cover {
    /* width: 1920px; */
    /* height: 1080px; */
    /* opacity: 0.5; */
    background: linear-gradient(to right, #DB646Fff, #ffffff80);
    ;
    padding-left: 244px;
    padding-top: 188px;
}

.team-title {
    /* Team */

    width: 640px;
    height: 62px;

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 65px;
    line-height: 117px;
    /* or 180% */
    display: flex;
    align-items: center;
    color: #FFFFFF;
    margin-bottom: 30px;
}

.team {
    width: 957px;
    height: 174px;
    display: flex;
    flex-wrap: wrap;

}

.team-member {
    /* Frame 30 */
    position: relative;
    width: 230px;
    height: 77px;

    /* background: rgba(255, 255, 255); */
    background-color: #f0d1d0;
    border-radius: 15px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 8px;
    margin-bottom: 15px;

}

.team-member .avatar {
    /* Ellipse 2 */

    width: 49px;
    height: 49px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
}

.team-member .member-title {
    /* Vien */
    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 88.9%;
    /* or 15px */
    text-align: center;
    text-transform: capitalize;
    color: #FFFFFF;
    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    margin-left: 9px;
}

.team-member .member-detail {
    /* display: none; */
    opacity: 0;
    position: absolute;
    /* transition: opacity 1s ease-in-out; */
}

.team-member:hover .member-detail {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 77px;
    left: 0px;
    /* Group 9 */
    padding: 25px;
    position: absolute;
    width: 314px;
    /* height: 311px; */
    /* Rectangle 4129 */
    box-sizing: border-box;
    background: #000;
    border: 0.5px solid #DCDCDC;
    border-radius: 15px;
    z-index: 1;
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}

.member-detail .twitter-logo {
    /* Frame 7346 */

    width: 27px;
    height: 26px;

    display: flex;
    justify-content: center;
    align-items: center;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    /* Rectangle 4131 */

    box-sizing: border-box;
    border: 0.5px solid #FFFFFF;
    border-radius: 4px;
}

.member-detail .twitter {
    /* Frame 7346 */
    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px 9px;
    gap: 9px;

    background: #272727;
    border-radius: 8px;
    color: white;
    width: 140px;
    margin-top: auto;

}

.member-detail .member-name {
    /* Vien */
    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 88.9%;
    /* identical to box height, or 20px */
    text-transform: capitalize;

    color: white;
}

.member-detail .member-job {
    /* Founder & CEO */

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 88.9%;
    /* or 12px */
    text-transform: capitalize;

    color: #fff;
    margin-top: 6px;
}

.angel-title {
    margin-top: 30px;
    /* Angel */
    width: 640px;
    height: 62px;
    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 65px;
    line-height: 117px;
    /* or 180% */
    display: flex;
    align-items: center;
    color: #FFFFFF;
}

.team-angel {
    margin-top: 30px;
    width: 957px;
    display: flex;
    flex-wrap: wrap;
}

.investors {
    margin-top: 25px;
}

.investors img {
    height: 80px;
    margin-right: 18px;
}

.team-member-intro {
    /* Ex-Head of BU Product in Tecent and Shanda Games */

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    /* or 120% */
    display: flex;
    align-items: center;
    color: #fff;
    margin-bottom: 13px;
}

.member-detail a{
    color:white;
    text-decoration: none;
}