.main-nav-container {
    display: flex;
    justify-content: center;
    /* background-color: #000000; */
    padding: 8px;
    /* background-image: url('../../assets/images/footer-bg.png');
     */
     background: radial-gradient( #9b51e070, #000000);
     /* background: linear-gradient(to right,#00000080, #9b51e080,#3081ED80, #00000080); */
     /* background: linear-gradient(to right, #00000080, #3081ED30, #9851ed30, #000000); */
}

.main-nav-content {
    display: flex;
    justify-content: center;
    /* background-color: #000000; */
    padding: 8px;
}

.main-nav {
    min-width: 375px;
    min-height: 200px;
    overflow: hidden;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
    position: relative;
    /* margin-bottom: 6px; */
    /* margin-right: 6px; */
}

.main-nav .nav-image {
    width: 100%;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
    /* width: 375px; */
    /* min-height: 200px; */
}

.main-nav .block-cover {
    z-index: 1;
    position: absolute;
    bottom: 4px;
    left: 0;
    opacity: 1;
    width: 375px;
    height: 200px;
    background: linear-gradient(30deg, #b14054, #b1405400, #b1405400);
}

.main-nav:nth-child(2) .block-cover {
    background: linear-gradient(30deg, #b1b85f, #b1b85f00, #b1b85f00);
}

.main-nav:nth-child(3) .block-cover {
    background: linear-gradient(30deg, #449cb2, #449cb200, #449cb200);
}

.main-nav:nth-child(4) .block-cover {
    background: linear-gradient(30deg, #e39962, #e3996200, #e3996200);
}

.main-nav:nth-child(5) .block-cover {
    background: linear-gradient(30deg, #8362b1, #8362b100, #8362b100);
}

.main-nav:nth-child(6) .block-cover {
    background: linear-gradient(30deg, #db64bf, #db64bf00, #db64bf00);
}


.main-nav .block-bar {
    position: absolute;
    height: 60px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
}

.main-nav .nav-title {
    height: 45px;
    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 28.2422px;
    line-height: 45px;
    display: flex;
    align-items: center;
    color: #FFFFFF;
    margin-left: 16px;
}

.main-nav .block-button {
    /* Button - Go to section */
    box-sizing: border-box;
    width: 64px;
    height: 64px;
    background: rgba(255, 255, 255, 0.07);
    border: 1px solid #FFFFFF;
    backdrop-filter: blur(9.5px);
    /* Note: backdrop-filter has minimal browser support */
    border-radius: 9999px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 16px;
    cursor: pointer;
}

.main-nav .block-button:hover {
    background: rgba(57, 51, 51, 0.47);
    border: 1px solid #FFFFFF;
    backdrop-filter: blur(9.5px);
    /* transform: translate(-2px,-2px);
    width: 70px;
    height: 70px; */
}