.quest-list {
    padding-top: 90px;
    position: relative;
    z-index: 999;
}

.quest-list-2 {
    margin-top: 50px;
}

.quest-list-box {
    width: calc(1436px - 48px);
    padding: 24px 16px 24px 16px;
    border-radius: 16px 16px 40px 40px;
    background: rgba(52, 39, 68, 0.28);
    margin-top: 10px;
}

.quest-drop-menu {
    margin-bottom: 12px;
}

.quest-list-item {
    background: rgba(245, 240, 255, 0.1);
    width: 100%;
    height: 90px;
    border-radius: 24px;
    margin-bottom: 12px;
}

.quest-list-item:last-child {
    margin-bottom: 0px;
}

.quest-list-left {
    margin-left: 28px;
    font-family: Bakbak One;
    font-size: 24px;
    font-weight: 400;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(245, 240, 255, 1);
}

.quest-list-right {
    margin-right: 28px;
}

.quest-list-checkbox {
    height: 42px;
}

.quest-list-checkbox>img,
.quest-2-img {
    display: inline-block;
    width: 34px;
    height: 34px;
}

.diamont-icon {
    width: 28px;
    height: 28px;
    display: inline-block;
    margin-left: 8px;
}

.diamont-text {
    font-family: Inter;
    font-size: 16px;
    font-weight: 800;
    line-height: 24px;
    letter-spacing: 0em;
    color: rgba(245, 240, 255, 1);
    margin-right: 8px;
}

.quest-list-btn {
    border-radius: 8px;
    cursor: pointer;
}

.quest-btn-1 {
    width: 122px;
    height: 44px;
    border: 1px solid rgba(245, 240, 255, 0.1);
    font-family: Inter;
    font-size: 16px;
    font-weight: 800;
    line-height: 42px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(245, 240, 255, 1);
    background: rgba(245, 240, 255, 0.1);
}

.quest-btn-2 {
    width: 115px;
    height: 42px;
    background: rgba(245, 240, 255, 0.1);
    color: rgba(222, 206, 255, 1);
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 42px;
    letter-spacing: 0em;
    text-align: center;
    border: 1px solid rgba(245, 240, 255, 0.1);
}

.quest-btn-3 {
    width: 89px;
    height: 42px;
    font-family: Poppins;
    background: rgba(53, 240, 208, 1);
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 42px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(49, 36, 66, 1);
}

.quest-btn-4 {
    width: 89px;
    height: 42px;
    background: rgba(175, 116, 245, 1);
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 42px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(49, 36, 66, 1);
}

.quest-list-label-pt {
    font-size: 30px;
    font-weight: 600;
    color: #fff;
}

.quest-list-label-text {
    font-family: Bakbak One;
    font-size: 30px;
    font-weight: 400;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: left;
    color: #fff;
    padding-left: 8px;
}

.quest-2-h {
    height: 77px;
}

.quest-2-item {
    height: 140px;
    background: rgba(245, 240, 255, 0.03);
}

.quest-2-left {
    width: 450px;
    height: 90px;
    padding: 11.5px 47px 12.5px 24px;
    border-radius: 20px;
    font-family: Poppins;
    font-size: 22px;
    font-weight: 500;
    line-height: 33px;
    letter-spacing: 0em;
    text-align: left;
    border: 1px solid rgba(175, 116, 245, 0.8);
    color: rgba(245, 240, 255, 1);
}

.dlg-floow-twitter-quest {
    /* POPUP */

    /* Auto layout */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    width: 600px;
    height: 406px;

    background: rgba(38, 34, 51, 0.95);
    border-radius: 12px;
}

.dftq-header {
    display: flex;
    padding: 16px;
}

.dftq-title {
    /* Aria Mission */

    margin: 0 auto;

    height: 28px;

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    /* identical to box height */

    color: #F1F1F1;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    text-align: left;

    margin-top: 35px;

}

.dftq-content {
    /* Frame 8985 */
    margin-top: 15px;
    box-sizing: border-box;

    width: 510px;
    height: 109px;

    background: #3D3949;
    border: 1px solid rgba(224, 224, 224, 0.7);
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 18px;
}

.dftq-content-logo {
    /* Frame 37904 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    flex: 1;

    width: 105px;
    height: 46px;

}

.dftq-content-logo>div:nth-child(1) {

    /* Frame 37902 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 8px;
    gap: 10px;

    width: 54px;
    height: 54px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    box-sizing: border-box;
}

.dftq-content-logo>div:nth-child(1)>img {
    /* image 114 */

    width: 38px;
    height: 38px;

    background: url('../../assets/images/x-logo1.png');
    border-radius: 20px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

}


.dftq-content-logo .name {

    /* Frame 37903 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 8px 8px 8px 0px;
    gap: 10px;

    width: 51px;



    /* Inside auto layout */
    flex: none;
    order: 1;
    flex-grow: 0;


    /* Aria */

    width: 43px;
    height: 31px;

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 31px;
    /* identical to box height */

    color: #F1F1F1;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

}

.dftq-content-username {
    /* Frame 8984 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px 0px 0px 5px;
    gap: 32px;


    width: 197px;
    height: 24px;


    /* @PlayAriaGame · Follow */

    width: 192px;
    height: 24px;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height */

    color: #C5C5C5;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

    /* Vector

    position: absolute;
    left: 2.5%;
    right: -2.5%;
    top: 5.69%;
    bottom: 5.74%;

    background: #FFFFFF; */
}

.dftq-content .xlogo>img {
    /* SVG */
    width: 38px;
    height: 46px;
    margin-right: 41px;
}

.dftq-follow-link {
    text-decoration: none;
    color: #35f0d0;
}

.dftq-footer {}

.dftq-validate-btn {
    /* Button */

    box-sizing: border-box;

    /* Auto layout */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 20px 92px;
    gap: 10px;


    width: 410px;
    height: 60px;


    background: #FFE4E4;
    border: 1px solid rgba(224, 224, 224, 0.7);
    border-radius: 10px;

    margin-top: 45px;

}