.header {
  border: 1px solid red;
}

.toolbar {
  margin-top: 25px;
  position: absolute;
  z-index: 20;
}

.toolbar-box {
  width: 1440px;
  height: 60px;
  border: 1px solid rgba(255, 255, 255, 0.17);
  border-radius: 14px;
  box-sizing: border-box;
  background: rgba(4, 11, 17, 0.39);
  -webkit-backdrop-filter: blur(6px);
  backdrop-filter: blur(6px);
}

.toolbar-box-shape {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.toolbar-slider-btn {
  width: 70px;
  height: 28px;
  background: linear-gradient(180deg, #d3a4db 0%, #9196e7 100%), #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 6px;
  margin-left: 15px;
  color: #ffffff;
  font-family: Bakbak One;
  font-size: 15px;
  font-weight: 400;
  line-height: 16px;
}

.toolbar-slider-btn > img {
  margin-right: 8px;
  width: 12px;
  height: 12px;
  display: inline-block;
}

.toolbar-slider-btn > span {
  margin-left: 11px;
}

.toolbar-logo-text {
  font-family: "Bakbak One";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 42px;
  text-transform: capitalize;
  color: #ffffff;
  margin-left: 24px;
}

.toolbar-drop-arrow {
  width: 60px;
  height: 60px;
  display: inline-block;
  object-fit: contain;
}

.toolbar-logo {
  width: 60px;
  height: 60px;
  display: inline-block;
  object-fit: contain;
  margin-left: 16px;
}

.toolbar-promot {
  height: 15px;
  right: -82px;
  top: 4px;
  font-family: Inter;
  font-style: italic;
  font-variation-settings: "slnt" -10;
  background: url(../../assets/images/promot-img.png) no-repeat;
  object-fit: cover;
  background-size: 100%;
  position: absolute;
}

.toolbar-promot-text {
  font-size: 12px;
  transform: scale(0.83);
  color: #ffffff;
  text-align: left;
  display: inline-block;
  font-family: Inter;
}

.toolbar-box-right {
}

.toolbar-login-menu {
  margin-right: 20px;
}

.toolbar-login-menu > span {
  font-family: Bakbak One;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
  margin-right: 4px;
}

.toolbar-login-menu-arrow {
  width: 12px;
  height: 12px;
  display: inline-block;
  margin-left: 4px;
}

.toolbar-login-menu-x {
  width: 20px;
  height: 24px;
  display: inline-block;
  margin-right: 4px;
}

.toolbar-login-btn {
  height: 38px;
  border-radius: 10px;
  color: #ffffff;
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
  position: relative;
  font-family: "Bakbak One";
}

.toolbar-login-btn > img {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-left: 24px;
}

.toolbar-login-btn-text {
  margin-right: 24px;
  margin-left: 8px;
  text-decoration: none;
}

.discord {
  width: 147px;
  background: #141b22;
  margin-right: 20px;
  text-decoration: none;
}

.connect {
  width: 133px;
  /* background: #af74f5; */
  margin-right: 20px;
  border: 1px solid rgba(255, 255, 255, 1);
}

.connect:hover {
  width: 133px;
  background: #af74f5;
  /* margin-right: 22px; */
  /* transform: translate(0px, -3px); */
}

.toolbar-login-btn.connected {
  background: #af74f5;
  margin-right: 22px;
  display: flex;
  padding-left: 29px;
  /* padding-right: 29px; */
}

.toolbar-login-btn.connected > .tl-btn-dropdown {
  padding: 15px 29px 15px 0px;
}

.connected .x-avatar {
  width: 32px;
  height: 32px;
}

.connected .x-name {
  width: 105px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.connected .dropdown:hover {
  transform: translateY(-2px);
}

.connected .x-disconnect {
  /* dropdown 下拉菜单 */

  box-sizing: border-box;

  /* Auto layout */
  display: flex;

  align-items: center;
  padding: 6px;
  padding-left: 14px;
  gap: 2px;

  position: absolute;
  width: 138px;
  height: 40px;
  right: 0px;
  top: 50px;
  color: #141b22;

  /* Gray 中性/White

主要容器背景/次要层级页面背景@bg-color-container
*/
  background: #ffffff;
  /* Gray 中性/Gray4-边框

1.默认边框@component-border
2.次要容器背景click@bg-color-secondarycontainer-active
3.组件背景hover/主要容器之上组件背景hover@bg-color-component-hover
*/
  border: 0.5px solid #dcdcdc;
  /* Shadow/Shadow-2 中层投影 */
  box-shadow: 0px 3px 14px 2px rgba(0, 0, 0, 0.05),
    0px 8px 10px 1px rgba(0, 0, 0, 0.06), 0px 5px 5px -3px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.connect-x-icon {
  width: 32px;
  height: 32px;
  border-radius: 20px;
  overflow: hidden;
  margin-right: 8px;
}

