.twitter-links {
  position: absolute;
  right: 0;
  top: 20px;
  color: black;
  /* dropdown 下拉菜单 */

  box-sizing: border-box;

  /* Auto layout */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 6px;
  gap: 2px;

  position: absolute;
  width: 160px;
  height: 70px;
  /* left: 1096px; */
  right: 0px;
  top: 41px;

  /* Gray 中性/White

主要容器背景/次要层级页面背景@bg-color-container
*/
  background: #ffffff;
  /* Gray 中性/Gray4-边框

1.默认边框@component-border
2.次要容器背景click@bg-color-secondarycontainer-active
3.组件背景hover/主要容器之上组件背景hover@bg-color-component-hover
*/
  border: 0.5px solid #dcdcdc;
  /* Shadow/Shadow-2 中层投影 */
  box-shadow: 0px 3px 14px 2px rgba(0, 0, 0, 0.05),
    0px 8px 10px 1px rgba(0, 0, 0, 0.06), 0px 5px 5px -3px rgba(0, 0, 0, 0.1);
  border-radius: 6px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.twitter-links a {
  text-decoration: none;
  color: black;
  font-size: 14px;
  line-height: 22px;
}
