.dlg-occi-main {
    /* POPUP */

    /* Auto layout */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    width: 600px;
    background: rgba(38, 34, 51, 0.95);
    border-radius: 12px;

    width: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;

}

.occi-header {
    display: flex;
    padding: 16px;
}

.occi-title {
    margin: 0 auto;
    height: 28px;
    width: 510px;
    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: #F1F1F1;
    /* flex: none; */
    order: 0;
    /* flex-grow: 0; */
    /* text-align: left; */
    margin-top: 35px;
    display: flex;
    justify-content: flex-start;

}

.occi-content {
    /* Frame 8985 */
    margin-top: 15px;
    box-sizing: border-box;

    width: 510px;
    height: 109px;
    background: #3D3949;
    border: 1px solid rgba(224, 224, 224, 0.7);
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 18px;
    justify-content: center;

}

.occi-content .occi-timer {
    /* 00：00：00：50 */
    /* width: 294px; */
    height: 53px;

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 38px;
    line-height: 53px;

    color: #F1F1F1;
}

.occi-content-logo {
    /* Frame 37904 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    flex: 1;

    width: 105px;
    height: 46px;
}

.occi-points {
    /* Text */

width: 126px;
height: 39px;

/* font-family: 'Inter'; */
font-style: normal;
font-weight: 800;
font-size: 26px;
line-height: 150%;
/* identical to box height, or 39px */

/* pt/Purple 50 */
color: #F5F0FF;


/* Inside auto layout */
flex: none;
order: 0;
flex-grow: 0;

}

.occi-content-logo>div:nth-child(1) {

    /* Frame 37902 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 8px;
    gap: 10px;

    width: 54px;
    height: 54px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
    box-sizing: border-box;
}

.occi-content-logo>div:nth-child(1)>img {
    /* image 114 */

    width: 38px;
    height: 38px;

    background: url('../../assets/images/x-logo1.png');
