.custom-layout {
  /* position:fixed; */
  position: -webkit-sticky; /* 兼容 WebKit 浏览器 */
  position: sticky;
  /* bottom:20px; */
  top: calc(100vh - 108px);
  margin-left: 14px;
  margin-bottom: 24px;
}
.custom-audio {
  overflow: hidden;
  width: 335px;
  height: 88px;
  border-radius: 12px;
  border: 1px;
  border: 1px solid rgba(236, 236, 236, 1);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  background: linear-gradient(0deg, #ececec, #ececec),
    linear-gradient(0deg, rgba(255, 255, 255, 0.84), rgba(255, 255, 255, 0.84));
}
.custom-audio-dash,
.custom-hide-audio {
  top: 0;
  left: 0;
}
.custom-audio-dash {
  z-index: 1;
  opacity: 1;
}
.custom-hide-audio {
  z-index: -1;
  opacity: 0;
  width: 335px;
  height: 88px;
  padding: 8px 16px 8px 8px;
  border-radius: 12px;
}
.custom-audio-cover {
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 80x;
  margin-top: 8px;
}
.custom-audio-info {
  width: 112px;
  margin-left: 8px;
  margin-right: 18px;
  margin-top: 8px;
}
.custom-audio-info-title {
  font-family: Helvetica Neue;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0em;
  color: rgba(15, 23, 42, 1);
  margin-top: 4px;
}
.custom-audio-info-desc {
  font-family: Helvetica Neue;
  font-size: 11px;
  font-weight: 700;
  line-height: 15px;
  letter-spacing: 0em;
  color: rgba(156, 163, 175, 1);
  white-space: nowrap;
  margin-top: 4px;
}
.custom-audio-controls {
}
.custom-audio-controls-last,
.custom-audio-controls-play,
.custom-audio-controls-next,
.custom-audio-controls-volume {
  width: 20px;
  height: 20px;
  display: inline-block;
}

.progress-slider {
  width: 263px;
  height: 12px;
  border-radius: 100px;
  margin-left: 8px;
}
.costom-audio-progress {
  height: 30px;
}
.progress-timer {
  font-family: Poppins;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.02em;
  text-align: right;
  color: rgba(147, 152, 157, 1);
}
