.gameplay {
    z-index: 0;
    position: relative;
}

.play-video {
    /* position: absolute; */
    /* width: 100%; */
    /* 宽度为100% */
    /* height: 100%; 高度为100% */
    /* position: fixed; */
    top: 0;
    object-fit: cover;
    margin-bottom: -4px;
    /* z-index: 0; */
}

.game-mask {
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
}

.play-button {
    left: calc(50% - 39px);
    position: absolute;
    top: 50%;
}

.play-bottom {
    /* position: absolute; */
    /* bottom: 100px; */
    left: 0;
    right: 0;
    height: 80px;
    color: white;
    display: flex;
    justify-content: space-between;
    padding-right: 240px;
    align-items: center;
    padding-bottom: 15px;
    position:sticky;
    top:calc(100% - 150px);
}

.play-youtube {
    border: 1px solid white;
    padding: 12px 26px;
    border-radius: 4px;
    display: flex;
    cursor: pointer;
}

.play-return {
    border: 1px solid white;
    height: 78px;
    width: 78px;
    border-radius: 78px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.play-video-title {
    margin-left: 132px;
}

.play-video-title .h1 {
    font-family: "Poppins";
    font-size: 32px;
    line-height: 40px;
    font-weight: 700;
}

.play-video-title .h2 {
    /* Medium Label-Medium 14px */
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    /* identical to box height, or 157% */
    letter-spacing: 0.005em;

    color: #9CA3AF;

    /* Inside auto layout */
    flex: none;
    order: 1;
    align-self: stretch;
    flex-grow: 0;
}

.game-youtube-link {
    text-decoration: none;
    color: white;
}