.terms-container {
    background-color: #000000;
    color: white;
}
.terms {
    background: radial-gradient( #9b51e070, #000000);
    padding-top: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.terms-content {
    width: 1200px;
    padding-bottom: 127px;
}

.terms-content h1 {
    font-size: 50px;
    line-height: 117px;
}

.terms-content p {
    color: #e7ecf2;
    margin-top: 12px;
    font-family: Poppins;
    font-size: 16px;
    line-height: 32px;
}

.terms-content h2 {
    margin-top: 43px;
}