.quest-userinfo {
    width: 480px;
    height: 333px;
    margin-top: 50px;
}

.userinfo-name {
    font-family: Bakbak One;
    font-size: 42px;
    font-weight: 400;
    line-height: 59px;
    letter-spacing: 0px;
    text-align: center;
    color: rgba(255, 255, 255, 1);
}

.userinfo-avatar {
    border: 2px solid rgba(255, 255, 255, 1);
    width: 140px;
    height: 140px;
    border-radius: 140px;
}

.userinfo-avatar>img {
    display: inline-block;
    width: 140px;
    height: 140px;
    border-radius: 100px;
}

.useinfo-button {
    width: 341px;
    height: 78px;
    border: 1px solid rgba(255, 255, 255, 1);
    background: linear-gradient(180deg, rgba(211, 164, 219, 0.59) 0%, rgba(145, 150, 231, 0.59) 100%);
    backdrop-filter: blur(2px);
    /* Note: backdrop-filter has minimal browser support */
    border-radius: 57px;
    margin-top: 29px;
}

.useinfo-button>img {
    display: inline-block;
    width: 36px;
    height: 36px;
    margin-left: 54px;
}

.useinfo-button>span {
    font-family: Inter;
    font-size: 36px;
    font-weight: 800;
    line-height: 44px;
    margin-right: 64px;
    letter-spacing: 0px;
}

.quest-no-connect {
    color: white;
    font-family: "Bakbak One";
    font-size: 42px;
    line-height: 59px;
}

.quest-connect-x-btn {
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 4px 15px;
    border-radius: 10px;
    /* Button */

    box-sizing: border-box;

    /* Auto layout */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 24px;
    gap: 8px;


    width: 133px;
    height: 48px;


    background: rgba(39, 39, 39, 0.15);
    border: 1px solid #FFFFFF;
    border-radius: 10px;
    margin-top: 31px;

    cursor: pointer;

}