
.app-footer{
    width: 100vw;
    height: 125px;
    background-color: #000;
    display: flex;
    justify-content: center;
}
.app-footer-tag{
    /* margin-left:238px; */
    width:500px;
    /* padding-left:24px; */
    flex-wrap: wrap;
}
.tag-item{
    padding: 10.5px 13px;
    font-family: Poppins;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    letter-spacing: 0em;
    text-align: center;
    color: #fff;
    border: 1px solid #9CA3AF;
    border-radius:36px;
    margin-top:2px;
    margin-right:4px;
}
.app-footer-copyright{
    width: 725px;
    font-family: Poppins;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0em;
    text-align: left;
    color: #fff;
    /* margin-right:238px; */
}