.road-map-container{
    width: 100%;
    height: 1066px;
    background: url(../../assets/images/roadmap-bg.png);
    object-fit: cover;
    top:0;
    left:0;
    z-index:000;
}
.road-map-content{
    position: absolute;
    top: 0;
    z-index:999;
    width: 100%;
    height:100%;
    background: linear-gradient(104.65deg, rgba(68, 156, 178, 0.95) 5.09%, rgba(68, 156, 178, 0) 100.71%);
    opacity: 0.85;
}
.road-content>h1{
    font-family: Bakbak One;
    font-size: 76px;
    font-weight: 400;
    line-height: 106px;
    letter-spacing: 0em;
    text-align: left;
}
.road-content>p{
    font-family: Poppins;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
}
.road-map-album{
    margin-top:47px;
}
.road-map-album>img{
    width: 198px;
    height: 400px;
    display: inline-block;
    object-fit: contain;
    margin-right:8px;
}
.last-button{
    bottom:0px;
    left:-136px;
}
.last-button>img{
    width: 78px;
    height:78px;
    display: inline-block;
}



@media screen and (min-width: 1440px) and (max-width: 1680px){
    .road-map-content>div{
        width: calc(100% - 240px);
        padding-top:158px;
        margin-left:120px;
    }
}
@media screen and (min-width: 1681px) and (max-width: 1920px){
    .road-map-content>div{
        width: calc(100% - 480px);
        padding-top:158px;
        margin-left:240px;
    }
}
/* 屏幕大于1921的时候 */
@media screen and (min-width: 1921px){
    .road-map-content>div{
        width: calc(100% - 480px);
        padding-top:158px;
        margin-left:240px;
    }
}