.welcome-referral {
    /* POPUP */

    /* Auto layout */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    position: absolute;
    width: 600px;
    height: 406px;
    left: calc(50% - 600px/2);
    top: calc(50% - 406px/2);

    background: rgba(38, 34, 51, 0.95);
    box-shadow: 0px 4px 20px rgba(255, 255, 255, 0.1);
    border-radius: 12px;

    color: white;
}

.welcome-header {
    /* Frame 37876 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    gap: 10px;
    /* width: 600px; */
    height: 60px;
    background: #262233;
    box-shadow: inset 0px -1px 0px rgba(255, 255, 255, 0.25);
    border-radius: 12px 12px 0px 0px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    align-self: stretch;
    flex-grow: 0;

}

.welcome-caption {
    /* Welcome to Aria Game ! */
    height: 28px;
    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    /* identical to box height */

    color: #F1F1F1;
}

.welcome-title {
    /* YOU GOT */

    width: 100px;
    height: 34px;

    font-family: 'Bakbak One';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 34px;
    /* identical to box height */

    color: #F1F1F1;
    margin-top: 35px;

    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

}

.welcome-points {
    /* Heading 2 ⏵ 400k+ */

    width: 223px;
    height: 72px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 45px;
    line-height: 72px;
    /* identical to box height, or 160% */
    display: flex;
    align-items: center;
    text-align: center;

    background: linear-gradient(180deg, #D3A4DB 0%, #9196E7 100%), #FFFFFF;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;

    margin-top: 16px;
}

.welcome-desc {
    /* Input a referrals code if you' ve got one for extra PTS */

    /* width: 412px; */
    height: 24px;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height */

    color: #F1F1F1;


    /* Inside auto layout */
    flex: none;
    order: 1;
    flex-grow: 0;
}

.welcome-input {
    /* Frame 5 */

    box-sizing: border-box;

    width: 400px;
    height: 55px;

    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(57, 57, 81, 0.5);
    border-radius: 10px;

    /* Inside auto layout */
    flex: none;
    order: 2;
    flex-grow: 0;
    display: flex;
    align-items: center;
    padding: 16px;
}

.welcome-input input {
    border: none;
    font-size: 16px;
    background-color: transparent;
    color: white;
    padding: 10px 0px;
}

.welcome-input .go-btn {
    /* Frame 194 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 6px 8px 7px;
    gap: 8px;
    width: 59px;
    height: 29px;
    background: #AF74F5;
    border-radius: 6px;
    margin-left: auto;
    box-sizing: border-box;
    cursor: pointer;
}