.quest-header {
    height: 100vh;
    background: url("../../assets/images/quest-bg.png") no-repeat;
    background-size: 100%;
    overflow: hidden;
    height: 600px;
    object-fit: cover;
}


.quest-content{
    width: 100vw;
    height:2400px;
    margin-top:-59px;
    position: relative;
 }
 .quest-list-container{
    position: relative;
    margin-top:40px;
    overflow: hidden;
 }
 .quest-content-bg{
   display: inline-block;
   position: absolute;
   z-index: 2;
   width: 100%;
   height:2438px;
   top:-38px;
 }
 .quest-content-cxt{
   width: 100vw;
   height:2400px;
   position:relative;
 }
 .quest-content-container{
   position:absolute;
   left: 0;
   width: 100vw;
   background: #140A21;
   z-index: 0;
   height: 2333px;
   top: 67px;
   overflow: hidden;
 }


 

