.dialog{
    animation-duration: 0.3s;
    animation-name: fadeInUp;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    display: none;
    position: fixed;
    z-index: 99;
    top: 0;
    left:0;
}

.dialog.nomask {
    background: none;
}

.hide{
    animation-duration: 0.3s;
    animation-name: fadeOut;
    display: none;
}
.show{
    display: block;
}
/* CSS样式 */
.dialog-enter-active, .dialog-leave-active {
  transition: opacity 1s ease; /* 定义动画持续时间、速度曲线等参数 */
}
.dialog-enter, .dialog-leave-to {
  opacity: 0; /* 初始状态和结束状态都设置为完全透明 */
}