/****混合****/
.box {
    height: 0.6em;
    border-radius: 0.6em;
    cursor: pointer;
  }
  .slider-containers {
    /*滑条最外层容器*/
    height: 12px;
    display: flex;
    font-size: 16px;
    justify-content: space-between;
    border-radius: 12px;
  }
  .slider-containers .slider-container {
    /*滑条容器*/
    height: 12px;
    border-radius: 12px;
    cursor: pointer;
    position: relative;
    width: 24em;
    background-color: #e4e7ed;
  }
  .slider-containers .slider-container .slider {
    /*滑条*/
    height: 0.6em;
    border-radius: 0.6em;
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 0;
    background-color: #AF74F5;
  }
  .slider-containers .slider-container .slider-btn {
    /*位置*/
    position: absolute;
    top: -0.3em;
    cursor: grab;
    user-select: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    border: 2px solid #AF74F5;
    background-color: white;
  }
  .slider-btn{
     transition: all ease-out 0.2s;
  }
  .slider{
    transition: width ease-out 0.2s;
 }
  
  .slider-containers .slider-container .slider-btn .info {
    display: none;
  }
  
  .slider-containers .slider-container .slider-btn .info {
    /*信息提示*/
    display: block;
    width: 1.8em;
    line-height: 1.8em;
    position: absolute;
    text-align: center;
    left: -0.3em;
    top: -2.4em;
    background-color: gray;
    color: red;
    border-radius: 0.2em;
  }
  
  .slider-containers .slider-container .slider-btn:active {
    cursor: grabbing;
  }
  .slider-containers .slider-container .slider-btn:active .info {
    display: none;
  }